.f-checkbox {
  display: inline-block; }

.f-checkbox__field {
  clip: rect(1px, 1px, 1px, 1px);
  position: absolute; }

.f-checkbox__label {
  user-select: none;
  font-size: 1em; }
  .f-checkbox__label {
    color: inherit;
    position: relative;
    display: flex;
    align-items: center;
    flex-wrap: nowrap;
    transition: color 0.2s cubic-bezier(0.51, 0.92, 0.24, 1);
    will-change: color; }
  .f-checkbox__field:not(:disabled) + .f-checkbox__label {
    cursor: pointer; }
  .f-checkbox__field:disabled + .f-checkbox__label {
    cursor: not-allowed; }
  .f-checkbox__label .f-checkbox__label-text {
    color: inherit; }
    .f-checkbox__field:hover + .f-checkbox__label .f-checkbox__label-text {
      color: inherit; }
    .f-checkbox__field:checked + .f-checkbox__label .f-checkbox__label-text {
      color: inherit; }
  .f-checkbox__label:before, .f-checkbox__label:after {
    content: '';
    display: block;
    font-size: 1em; }
  .f-checkbox__label:before {
    color: silver;
    border: 2px solid currentColor;
    border-radius: 0.15em;
    margin-right: 1em;
    width: 1.2em;
    height: 1.2em;
    display: block;
    transition: color 0.2s cubic-bezier(0.51, 0.92, 0.24, 1);
    will-change: color;
    flex-shrink: 0; }
  .f-checkbox__field:not([disabled]).has-error + .f-checkbox__label:before {
    color: #d33944; }
  .f-checkbox__field:disabled + .f-checkbox__label:before {
    color: silver; }
  .f-checkbox__label:after {
    transform: scaleX(-1) translate(-0.22em, 0) rotate(135deg);
    transform-origin: top left;
    visibility: hidden;
    transition: color 0.2s cubic-bezier(0.51, 0.92, 0.24, 1);
    will-change: color;
    border-right: 2px solid currentColor;
    border-top: 2px solid currentColor;
    position: absolute;
    left: 0;
    top: 50%; }
  .f-checkbox__field:checked + .f-checkbox__label:after {
    animation: check 0.35s forwards;
    visibility: visible;
    will-change: width, height; }

@keyframes check {
  0% {
    height: 0;
    width: 0; }
  25% {
    height: 0;
    width: 0.35em; }
  50%,
  100% {
    height: 0.7em;
    width: 0.35em; } }

.f-checkbox__label-text {
  color: gray;
  font-size: 1em;
  line-height: 1; }
  .f-checkbox__field[disabled].f-checkbox__field[disabled] + .f-checkbox__label .f-checkbox__label-text {
    color: silver; }

.f-checkbox__indicator {
  pointer-events: none;
  user-select: none;
  color: silver; }
  .f-checkbox__indicator--optional {
    font-size: 0.5em; }
  .f-checkbox__indicator--required {
    font-size: 1em; }
  .has-error .f-checkbox__field:hover ~ .f-checkbox__indicator,
  .has-error .f-checkbox__field:focus ~ .f-checkbox__indicator {
    color: #d33944 !important; }
  .f-checkbox__field[disabled].f-checkbox__field[disabled] ~ .f-checkbox__indicator {
    color: silver; }

.f-checkbox__message {
  font-size: 0.8em; }
  .f-checkbox__message--instruction {
    color: gray; }
  .f-checkbox__message--error {
    color: #d33944; }
    .has-error .f-checkbox__message--error::-moz-selection {
      color: white;
      background: #d33944 !important;
      text-shadow: none; }
    .has-error .f-checkbox__message--error::selection {
      color: white !important;
      background: #d33944 !important;
      text-shadow: none; }
  .f-checkbox__message--error + .f-checkbox__message--instruction {
    display: none; }
